


































import {
  Component, Emit, Prop, Vue, Watch,
} from 'vue-property-decorator';
import { DonationPoint, DonationPointPosition } from '@/models/DonationPoint';
import { mapSettings, POINT_MARKER_ICON_CONFIG } from '@/constants/mapSettings';
import colorPalette from '@/constants/colorPalette';
import DonationPointItemSelected from '@/components/DonationPointItemSelected.vue';

@Component({
  components: { DonationPointItemSelected },
})
export default class DonationPointsMap extends Vue {
    @Prop() private markers!: DonationPoint[];

    @Prop() private selectedPoint!: DonationPoint;

    @Prop() userPosition?: DonationPointPosition;

    private zoom = 12;

    private overPoint?: DonationPoint;

    // eslint-disable-next-line class-methods-use-this
    data(): object {
      return { overPoint: null };
    }

    // eslint-disable-next-line class-methods-use-this
    get mapConfig() {
      return {
        ...mapSettings,
      };
    }

    get mapCenter() {
      return this.userPosition || this.selectedPoint?.position;
    }

    get markersWithUser(): DonationPoint[] {
      if (!this.userPosition) {
        return this.markers;
      }
      return [this.getUserMarker(this.userPosition), ...this.markers];
    }

    // eslint-disable-next-line class-methods-use-this
    private getUserMarker(userPosition: DonationPointPosition): DonationPoint {
      return { position: userPosition, title: '', description: '' };
    }

    @Emit()
    // eslint-disable-next-line class-methods-use-this
    selectPoint(marker: DonationPoint) {
      return marker;
    }

    @Watch('selectedPoint', { immediate: true, deep: true })
    selectedPointChanged(newSelectedPoint?: DonationPoint) {
      if (newSelectedPoint) {
        this.fitUserAndMarker(newSelectedPoint);
      }
    }

    mounted(): void {
      // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
      // @ts-ignore
      this.$refs.mapRef.$mapPromise.then((map: google.maps.Map) => {
        map.addListener('zoom_changed', () => {
          this.zoom = map.getZoom();
        });
      });
      this.fitUserAndMarker(this.selectedPoint);
    }

    fitUserAndMarker(marker: DonationPoint) {
      if (this.userPosition && this.$refs.mapRef) {
        // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
        // @ts-ignore
        this.$refs.mapRef.$mapPromise.then((map: google.maps.Map) => {
        // eslint-disable-next-line no-undef
          const bounds = new google.maps.LatLngBounds();
          if (this.userPosition) {
            bounds.extend(this.userPosition);
          }
          bounds.extend(marker.position);
          map.fitBounds(bounds, 100);
        });
      }
    }

    iconOptions(marker: DonationPoint) {
      const scale = this.zoom / 3;

      return {
        ...POINT_MARKER_ICON_CONFIG,
        scale,
        fillColor: this.isUserMarker(marker) || this.isSelectedMarker(marker)
          ? colorPalette.PRIMARY
          : colorPalette.SECONDARY,
        strokeColor: this.isUserMarker(marker) || this.isSelectedMarker(marker)
          ? colorPalette.PRIMARY
          : colorPalette.SECONDARY,
      };
    }

    labelOptions(marker: DonationPoint, index: number) {
      return this.isUserMarker(marker)
        ? undefined
        : {
          text: index.toString(),
          fontSize: `${this.zoom / 6}em`,
          color: this.isSelectedMarker(marker)
            ? colorPalette.SECONDARY
            : colorPalette.PRIMARY,
        };
    }

    isSelectedMarker(marker: DonationPoint): boolean {
      return this.selectedPoint.description === marker.description;
    }

    isUserMarker(marker: DonationPoint): boolean {
      return marker.position.lng === this.userPosition?.lng
        && marker.position.lat === this.userPosition?.lat;
    }
}
